import { Component, OnInit } from '@angular/core';
import { TimepickerModule, BsDatepickerModule } from 'ngx-bootstrap'
import { ServiceService } from '../-service.service';
import { EmployeeForm } from '../employee-form';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-timeentered',
  templateUrl: './timeentered.component.html',
  styleUrls: ['./timeentered.component.scss']
})
export class TimeenteredComponent implements OnInit {
  today: number = Date.now();
  public employeeData:EmployeeForm[]=[];
  public employees:EmployeeForm[] =[]
  constructor(private _service:ServiceService,private spinner:NgxSpinnerService) { }

  ngOnInit() {
this.spinner.show();
this.getData();
  }


getData(){
this.employeeData = JSON.parse(localStorage.getItem('currentUser'));
if(this.employeeData.length>0){
console.log(this.employeeData);
  this.employeeData.forEach(element => {
    this.employees.push(element);
  });
  setTimeout(() => {
    /** spinner ends after 5 seconds */
    this.spinner.hide();
  }, 1000);
}  
  
}

}
