import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { NewtimeentryComponent } from './newtimeentry/newtimeentry.component';
import { TimeenteredComponent } from './timeentered/timeentered.component';
import { SignoutComponent } from './signout/signout.component';

const routes: Routes = [
  { path: '', redirectTo: '/newtimeentry', pathMatch: 'full' },
  { path:'newtimeentry', component: NewtimeentryComponent },
  { path:'timeentered', component: TimeenteredComponent },
  { path: 'signout', component: SignoutComponent }


];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
