import { Component, OnInit } from '@angular/core';
import { TimepickerModule, BsDatepickerModule } from 'ngx-bootstrap'
import { FormGroup, FormControl } from '@angular/forms';
import { ServiceService } from '../-service.service';
import { Router } from '@angular/router';
import { EmployeeForm } from '../employee-form';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-newtimeentry',
  templateUrl: 'newtimeentry.component.html',
  styleUrls: ['newtimeentry.component.scss']
})
export class NewtimeentryComponent implements OnInit {
  empolyeeForm:FormGroup;

  public employeeList:EmployeeForm[]=[];
  public emplist : EmployeeForm[]=[];
  constructor(private _service:ServiceService,private router: Router,private spinner: NgxSpinnerService) { }

  ngOnInit() {
    
    this.empolyeeForm =new FormGroup({

        employeeName:new FormControl(),
        date:new FormControl(),
        timeworked:new FormControl()
        
    })
  }
  Onsubmit(){
    this.spinner.show();
    this.emplist =JSON.parse(localStorage.getItem('currentUser'));
    if(this.emplist!=null){
      this.emplist.forEach(e=>{
        this.employeeList.push(e);
      })
    }
    this.employeeList.push(this.empolyeeForm.value);
    localStorage.setItem('currentUser', JSON.stringify(this.employeeList));
    this.router.navigate(['/timeentered']);
    setTimeout(() => {
      /** spinner ends after 5 seconds */
      this.spinner.hide();
    }, 1000);
   
    
    
       
  }
}
